/* Main CSS file */
@import '~slick-carousel/slick/slick.css';
@import '~slick-carousel/slick/slick-theme.css';

body {
  font-family: 'DotGothic16', sans-serif;
  background-color: #9a9988;
  color: black;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.window {
  border: 2px solid black;
  background-color: rgb(243, 242, 241);
  padding: 100px;
  box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.7);
  min-height: 100vh; /* Ensure window fills the screen */
  display: flex;
  flex-direction: column;
}

.window-content {
  margin-top: 20px;
  flex-grow: 1; /* Allow content to grow and fill available space */
}

.flipped-horizontal {
  transform: scaleX(-1);
}

.centered-content {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.shopHeader {
  text-align: center;
  margin: 0 10px;
  padding: 10px;
}

.side-image {
  width: 90px;
  height: 100px;
}

.button {
  background-color: #c3c7c9;
  border: 2px solid #808080;
  box-shadow: 1px 1px 0px rgba(255, 255, 255, 0.7);
  padding: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: #b0b0b0;
}

.nav-link {
  text-decoration: none;
  color: inherit;
}

.nav-link:visited {
  color: inherit;
}

.nav-link:hover {
  color: inherit;
}

.nav-link:active {
  color: inherit;
}

.group-photo {
  border-radius: 5px;
}

.barbers-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  padding: 20px;
}

.person-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Space between elements */
  text-align: center;
  background-color: #f0f0e8;
  border: 2px solid black;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.7);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  overflow: hidden;
  min-height: 450px; /* Set a minimum height for uniformity */
  position: relative;
}

.carousel-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.about-carousel-slide {
  width: 100%; /* Ensure the image takes the full width of its container */
  height: 100%; /* Make sure the image fills the height of the container */
  object-fit: cover; /* Ensure the image covers the entire area without being distorted */
  border-radius: 10px;
}

.carousel-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 10px;
}

.about-carousel-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 10px;
}

.person-image {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid black;
  margin-bottom: 15px;
  object-fit: cover;
}

.instagram-link {
  display: inline-flex;
  align-items: center;
  justify-content: center; /* Center the Instagram link */
  text-decoration: none;
  color: black;
  font-weight: bold;
  margin-top: 10px; /* Add some space between the image and username */
}

.instagram-icon {
  width: 20px;
  height: 20px;
  margin-right: 8px;
}

.slick-dots {
  bottom: 10px;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .window {
    padding: 20px;
    min-height: 100vh; /* Ensure the window still fills the screen on mobile */
  }

  .barbers-grid {
    grid-template-columns: 1fr; /* Stack cards vertically on mobile */
    gap: 20px;
    padding: 20px;
    padding-bottom: 20px;
  }

  .person-image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 2px solid black;
    margin-bottom: 15px;
    object-fit: cover;
  }

  .shopHeader {
    font-size: 1.5em; /* Adjust font size for mobile */
    margin: 10px;
  }

  .carousel-slide {
    height: 150px; /* Adjust height for mobile */
  }

  .carousel-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
  }

  .group-photo {
    width: 100%; /* Make the group photo responsive on mobile */
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 2%;
  }

  .slider{
    height: 100%;
  }

  .slick-prev, .slick-next {
    color: black; /* Change the arrow color to dark (black) */
    font-size: 30px; /* Adjust size if needed */
    z-index: 1; /* Ensure arrows appear above other elements */
  }
  
  
  /* Optional: Positioning the arrows */
  .slick-prev {
    left: 10px; /* Adjust positioning for the previous arrow */
  }
  
  .slick-next {
    right: 10px; /* Adjust positioning for the next arrow */
  }
}
